import { feature } from 'topojson-client';
import { csvParse, autoType } from 'd3-dsv';

export async function getData(url) {
  let response = await fetch(url);
  let string = await response.text();
	let data = await csvParse(string, autoType);
  return data;
}

export async function getTopo(url, layer) {
  let response = await fetch(url);
  let topojson = await response.json();
	let geojson = await feature(topojson, layer);
  return geojson;
}

export async function getNomis(url, code) {
  let response = await fetch(url);
  let string = await response.text();
	let data = await csvParse(string, (d) => {
		return {
			code: d['GEOGRAPHY_CODE'],
			value: +d[code],
			count: +d['0'],
			perc: (+d[code] / +d['0']) * 100
		};
	});
  return data;
}

export function aggregate(data, lookup) {
	let indexed = {};
	data.forEach(d => {
		let parent = lookup[d.code].parent;
		if (!indexed[parent]) {
			indexed[parent] = {
				value: d.value,
				count: d.count
			};
		} else {
			indexed[parent].value += d.value;
			indexed[parent].count += d.count;
		}
	});

	let newdata = [];
	let keys = Object.keys(indexed);
	keys.forEach(key => {
		newdata.push({
			code: key,
			value: indexed[key].value,
			count: indexed[key].count,
			perc: (indexed[key].value / indexed[key].count) * 100
		});
	});

	return newdata;
}

export function getBreaks(data) {
	let count = data.length;
	
	let breaks = [
		data[0],
		data[Math.floor(count * 0.1)],
		data[Math.floor(count * 0.2)],
		data[Math.floor(count * 0.3)],
		data[Math.floor(count * 0.4)],
		data[Math.floor(count * 0.5)],
		data[Math.floor(count * 0.6)],
		data[Math.floor(count * 0.7)],
		data[Math.floor(count * 0.8)],
		data[Math.floor(count * 0.9)],
		data[count - 1]
	];

	return breaks
}