<script>
	import { LayerCake, Svg, Html } from 'layercake';
	import { scaleBand } from 'd3-scale';

	import Column from './Column.svelte';
	import AxisY from './AxisY.svelte';

	export let data;
	export let yMax = null;
	export let yMin = 0;
  export let selected;

  export let xKey = 'code';
  export let yKey = 'perc';
	
	$: yMin = yMin > 0 ? 0 : yMin;
	
	let xDomain = [];

	data.forEach(d => {
		xDomain.push(d[xKey]);
		d[yKey] = +d[yKey];
	});
</script>

<style>
	.chart-container {
		width: 100%;
		height: 100px;
		margin-top: 25px;
	}
</style>

<div class="chart-container">
	<LayerCake
		padding={{ top: 0, right: 0, bottom: 20, left: 20 }}
		x={xKey}
		y={yKey}
		xScale={scaleBand()}
	  xDomain={xDomain}
		yDomain={[yMin, yMax]}
		data={data}
	>
		<Svg>
			<Column
				{selected}
			/>
			<AxisY
				ticks={2}
			/>
		</Svg>
	</LayerCake>
</div>