<script>
	import { getContext } from 'svelte';

	const { data, xGet, yGet, yRange, yDomain, xScale } = getContext('LayerCake');
	
  const min = $yDomain[0] < 0 ? {perc: 0} : {perc: $yDomain[0]};
	
	export let selected = null;

	$: columnWidth = d => {
		const vals = $xGet(d);
		return Math.max(0, (vals[1] - vals[0]));
	};

	$: columnHeight = d => {
		return $yGet(min) - $yGet(d) < 0 ? $yGet(d) - $yGet(min) : $yGet(min) - $yGet(d);
	};
	
	$: columnPos = d => {
		return $yGet(d) > $yGet(min) ? $yGet(min) : $yGet(d);
	}

	/* --------------------------------------------
	 * Default styles
	 */
	export let fill = 'rgb(0, 97, 180)';
	export let stroke = '';
	export let strokeWidth = 0;
	
  let selectedElement = null;
  
  function getSelected() {
    if (selected) {
      selectedElement = $data.find(d => d.code == selected);
    } else {
      selectedElement = null;
    }
  }

  $: selected && getSelected();

</script>

<g class="column-group" opacity=1>
	{#each $data as d, i}
		<rect
			class='group-rect'
			data-id="{i}"
			x="{$xScale.bandwidth ? $xGet(d) : $xGet(d)[0]}"
			y="{columnPos(d)}"
			width="{$xScale.bandwidth ? $xScale.bandwidth() : columnWidth(d)}"
			height="{columnHeight(d)}"
			fill="{d.color ? d.color : fill}"
			{stroke}
			stroke-width="{strokeWidth}"
		/>
	{/each}
</g>
{#if selectedElement}
<g class="column-group">
  <rect
		class='group-rect'
		x="{$xScale.bandwidth ? $xGet(selectedElement) : $xGet(selectedElement)[0]}"
		y="{columnPos(selectedElement) - 0.5}"
		width="{$xScale.bandwidth ? $xScale.bandwidth() + 2 : columnWidth(selectedElement) + 2}"
		height="{columnHeight(selectedElement)}"
		fill="rgb(0, 0, 0)"
		{stroke}
		stroke-width="{strokeWidth}"
	/>
</g>
{/if}