<script>
	import Radio from './Radio.svelte';

	export let expanded = false;
	export let props;
	export let selected;

	function toggle() {
		expanded = !expanded;
	}
</script>

<style>
	span {
		padding: 0 0 0 1.5em;
		background: url(./icons/folder.svg) 0 0.1em no-repeat;
		background-size: 1em 1em;
		font-weight: bold;
		cursor: pointer;
	}

	.expanded {
		background-image: url(./icons/folder-open.svg);
	}

	ul {
		padding: 0.2em 0 0 0.5em;
		margin: 0 0 0 0.5em;
		list-style: none;
		border-left: 1px solid #eee;
	}

	li {
		padding: 0.2em 0;
	}
</style>

<span class:expanded on:click={toggle}>{props.name}</span>
<small>{#if props.code}({props.code}){/if}</small>

{#if expanded}
	<ul>
		{#each props.children as child}
			<li>
				{#if child.type === 'group'}
					<svelte:self props={child} bind:selected={selected}/>
				{:else}
					<Radio props={child} bind:selected={selected}/>
				{/if}
			</li>
		{/each}
	</ul>
{/if}