<script>
	import { onMount } from 'svelte';
	import Panel from './Panel.svelte';
	import Group from './Group.svelte';
	import Map from './Map.svelte';
	import MapSource from './MapSource.svelte';
	import MapLayer from './MapLayer.svelte';
	import ColChart from './charts/ColChart.svelte';
	import Loader from './Loader.svelte';
	import { getData, getNomis, getBreaks, getTopo, aggregate } from './utils.js';
	
	// CONSTANTS
	const apiurl = 'https://www.nomisweb.co.uk/api/v01/dataset/';
	const apikey = '0x3cfb19ead752b37bb90da0eb3a0fe78baa9fa055';
	const geography = 'TYPE298';
	const mapstyle = 'https://bothness.github.io/ons-basemaps/data/style-omt.json';
	const tabledata = './data/indicators.json';
	const ladtopo = {
		url: './data/lad_boundaries_2020.json',
		layer: 'LA2020EW',
		code: 'AREACD',
		name: 'AREANM'
	};
	const lsoabldg = {
		url: 'https://cdn.ons.gov.uk/maptiles/t18/tiles/{z}/{x}/{y}.pbf',
		layer: 'imddata2',
		code: 'lsoa11cd'
	};
	const lsoabounds = {
		url: 'https://cdn.ons.gov.uk/maptiles/administrative/lsoa/v1/boundaries/{z}/{x}/{y}.pbf',
		layer: 'boundaries',
		code: 'AREACD'
	};
	const lsoadata = './data/lsoa2011_lad2020.csv';
	const colors = ["#d5f690","#5bc4b1","#2e9daa","#0079a2","#005583"];
	
	// OBJECTS
	let map = null;

	// DATA
	let indicators;
	let ladbounds;
	let ladlookup;
	let lsoalookup;
	let data = {};
	let active = {
		selected: null,
		hovered: null,
		highlighted: null
	};
	
	// STATE
	let selectItem;
	let selectMeta;
	let selectData;
	let selectLSOA;
	let selectLAD;
	let loading = true;

	let mapZoom = null;
	
	function initialise() {
		getTopo(ladtopo.url, ladtopo.layer)
		.then(geo => {
			ladbounds = geo;

			let lookup = {};
			geo.features.forEach(f => {
				lookup[f.properties[ladtopo.code]] = f.properties[ladtopo.name];
			});
			return lookup;
		})
		.then(lookup => {
			ladlookup = lookup;
			
			getData(lsoadata)
			.then(data => {
				let lookup = {};
				data.forEach(d => {
					lookup[d.code] = {
						name: d.name,
						parent: d.parent
					};
				});
				return lookup;
			})
			.then(lookup => {
				lsoalookup = lookup;

				fetch(tabledata)
				.then(res => res.json())
				.then(json => {
					indicators = json;

					selectItem = indicators[0].children[4].children[1];
				});
			})
		});
	}

	function setSelect() {
		if (!(selectMeta && selectItem && selectMeta.code == selectItem.code)) {
			let code = selectItem.code;
			let group = indicators.find(d => d.code == code.slice(0, 3));
			let table = group.children.find(d => d.code == code.slice(0, 7));
			let cell = +code.slice(7,10);

			selectMeta = {
				code: selectItem.code,
				group: group,
				table: table,
				cell: cell
			};

			loadData();
		}
	}

	function loadData() {
		if (data[selectItem.code]) {
			selectData = data[selectItem.code];
			console.log('data loaded from memory!');
		} else {
			loading = true;
			getNomis(apiurl + selectMeta.table.nomis + selectMeta.cell + '&geography=' + geography + '&uid=' + apikey, selectMeta.cell)
			.then(res => {
				let dataset = {
					lsoa: {},
					lad: {}
				};
				res.sort((a, b) => a.perc - b.perc);
				dataset.lsoa.data = res;
				let breaks = getBreaks(res.map(d => d.perc));
				dataset.lsoa.breaks = breaks;

				dataset.lsoa.data.forEach(d => {
					if (d.perc <= breaks[2]) {
						d.color = colors[0];
					} else if (d.perc <= breaks[4]) {
						d.color = colors[1];
					} else if (d.perc <= breaks[6]) {
						d.color = colors[2];
					} else if (d.perc <= breaks[8]) {
						d.color = colors[3];
					} else {
						d.color = colors[4];
					}
				});

				dataset.lad.data = aggregate(res, lsoalookup);
				dataset.lad.breaks = getBreaks(dataset.lad.data.map(d => d.perc));

				data[selectItem.code] = dataset;
				selectData = dataset;
				console.log('data loaded from nomis!');
				loading = false;
			});
		}
	}

	function setLSOA() {
		if (active.hovered && data[selectItem.code]) {
			selectLSOA = data[selectItem.code].lsoa.data.find(d => d.code == active.hovered);
			let ladcode = lsoalookup[active.hovered].parent;
			selectLAD = data[selectItem.code].lad.data.find(d => d.code == ladcode);
			active.highlighted = lsoalookup[active.hovered].parent;
		} else {
			selectLSOA = null;
			selectLAD = null;
			active.highlighted = null;
		}
	}

	$: selectItem && setSelect(); // Update meta when selection updates
	$: active.hovered && setLSOA(); // Get data for selected LSOA

	onMount(() => initialise());

</script>

<style>
	p {
		margin-bottom: 0;
	}
	ul {
		padding: 0.2em 0 0 1.2em;
		margin: 0;
	}

	li {
		padding: 0.2em 0 0 0;
	}

	#infobox {
		min-height: 100px;
	}

	.muted {
		color: #888;
	}

	.mt {
		margin-top: 5px;
	}
</style>

{#if loading}
<Loader height="100vh" width="100vw" position="fixed" bgcolor="rgba(255, 255, 255, 0.7)"/>
{/if}

<Panel>
	<h1>Census Data Atlas</h1>
	{#if indicators && selectItem}
	{#if data[selectItem.code]}
	<ColChart data={data[selectItem.code].lsoa.data} yMax={+data[selectItem.code].lsoa.breaks[10]} yMin={+data[selectItem.code].lsoa.breaks[0]} selected={selectLSOA ? selectLSOA.code : null} xKey="code" yKey="perc" />
	{/if}
	<div id="infobox">
	{#if selectLSOA && selectLAD}
		<strong>{lsoalookup[selectLSOA.code].name}</strong><br/>
		{selectLSOA.perc.toFixed(1)}% <span class="muted">({selectLAD.perc.toFixed(1)}%)</span> {selectItem.name.toLowerCase()}<br/>
		<small>{selectLSOA.value} of {selectLSOA.count} <span class="muted">({selectLAD.value} of {selectLAD.count})</span> {selectItem.unit.toLowerCase()}s</small>
		<p class="mt"><small class="muted">(LAD-level indicators in parentheses)</small></p>
	{/if}
	</div>

	<Group props={{name: "2011 Census Tables", children: indicators}} bind:selected={selectItem} expanded/>
	
	{#if selectMeta}
	<p><strong>Selected indicator:</strong></p>
	<small><ul>
		<li>{selectMeta.group.name}</li>
		<ul>
			<li>{selectMeta.table.name} <small>({selectMeta.table.code})</small></li>
			<ul>
				<li>{selectItem.name}</li>
			</ul>
		</ul>
	</ul></small>
	{/if}
	{/if}
</Panel>

<Map bind:map={map} style={mapstyle} minzoom={8} maxzoom={14} bind:zoom={mapZoom}>
	{#if selectData}
	<MapSource id="lsoa" type="vector" url={lsoabldg.url} layer={lsoabldg.layer} promoteId={lsoabldg.code} minzoom={8} >
		<MapLayer id="lsoa" source="lsoa" sourceLayer={lsoabldg.layer} data={selectData} type="fill" paint="{{
			'fill-color': ['case',
				['!=', ['feature-state','color'], null], ['feature-state','color'],
				'rgba(255, 255, 255, 0)'
			]
		}}" order="tunnel_motorway_casing" />
	</MapSource>
	<MapSource id="lsoa-bounds" type="vector" url={lsoabounds.url} layer={lsoabounds.layer} promoteId={lsoabounds.code} minzoom={8} >
		<MapLayer id="lsoa-fill" source="lsoa-bounds" sourceLayer={lsoabounds.layer} type="fill" paint="{{
			'fill-color': 'rgba(255, 255, 255, 0)'
		}}" hover={true} bind:hovered={active.hovered} />
		<MapLayer id="lsoa-bounds" source="lsoa-bounds" sourceLayer={lsoabounds.layer} type="line" paint="{{
			'line-color': ['case',
				['==', ['feature-state','hovered'], true], 'rgba(0, 0, 0, 1)',
				'rgba(0, 0, 0, 0)'
			],
			'line-width': ['case',
				['==', ['feature-state','hovered'], true], 2,
				0
			]
		}}" />
	</MapSource>
	{/if}
	{#if ladbounds}
	<MapSource id="lad" type="geojson" data={ladbounds} promoteId={ladtopo.code} >
		<MapLayer id="lad" source="lad" type="line" highlight={true} highlighted={active.highlighted} paint="{{
			'line-color': ['case',
				['==', ['feature-state','highlighted'], true], 'rgba(128, 128, 128, 1)',
				'rgba(128, 128, 128, 0)'
			],
			'line-width': ['case',
				['==', ['feature-state','highlighted'], true], 1.5,
				0
			]
		}}" order="highway_name_other" />
	</MapSource>
	{/if}
</Map>